.banner {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  background-color: #323032 !important;
  color: #ffffff;
  z-index: 2;
}

@media screen and (max-width: 1007px) {
  .banner {
    width: 90%;
  }
}
